// constants
import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
import {isMobile} from 'react-device-detect';
// log
import { fetchData } from "../data/dataActions";

const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};

const updateAccountRequest = (payload) => {
  return {
    type: "UPDATE_ACCOUNT",
    payload: payload,
  };
};

const trackEvent = (event) => {
  if (window && window.gtag) {
    window.gtag('event', 'click', {
      event_category: 'BOWCMintDappActions',
      event_label: event.label,
      value: event.value,
    });
  }
};

export const connect = () => {
  return async (dispatch) => {
    dispatch(connectRequest());
    const abiResponse = await fetch("/config/abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const abi = await abiResponse.json();
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const CONFIG = await configResponse.json();
    const { ethereum } = window;
    const metamaskIsInstalled = ethereum && ethereum.isMetaMask;
    if (metamaskIsInstalled) {
      Web3EthContract.setProvider(ethereum);
      let web3 = new Web3(ethereum);
      try {
        const accounts = await ethereum.request({
          method: "eth_requestAccounts",
        });
        const networkId = await ethereum.request({
          method: "net_version",
        });
        if (networkId == CONFIG.NETWORK.ID) {
          const SmartContractObj = new Web3EthContract(
            abi,
            CONFIG.CONTRACT_ADDRESS
          );
          dispatch(
            connectSuccess({
              account: accounts[0],
              smartContract: SmartContractObj,
              web3: web3,
            })
          );
          // Add listeners start
          ethereum.on("accountsChanged", (accounts) => {
            dispatch(updateAccount(accounts[0]));
          });
          ethereum.on("chainChanged", () => {
            window.location.reload();
          });
          // Add listeners end
        } else {
          dispatch(connectFailed(`Change network to ${CONFIG.NETWORK.NAME}.`));
        }
        trackEvent({
          label: 'Metamask Available Success',
          value: 'WalletConnect'
        });
      } catch (err) {
        trackEvent({
          label: 'Metamask Available Error',
          value: 'WalletFailedConnect'
        });
        dispatch(connectFailed("Something went wrong."));
      }
    } else {
      trackEvent({
        label: 'Metamask Not Available',
        value: 'WalletFailedConnect'
      });

      if (isMobile) {
        trackEvent({
          label: 'Metamask Not Available',
          value: 'WalletConnectRedirectMetaMask'
        });

        dispatch(connectFailed("Click the link below to mint using MetaMask Mobile."));
        // dispatch(connectFailed("Opening MetaMask on Your mobile. Click the menu top left, click browser, click +, and enter https://mint.boredofworkclub.io. That's it happy minting!"));
        // window.location.href = 'https://metamask.app.link/dapp/mint.boredofworkclub.io';
        window.open('https://metamask.app.link/dapp/mint.boredofworkclub.io', '_system');

        return;
      }
      
      dispatch(connectFailed("Install Metamask."));
    }
  };
};
